import React, { Component } from "react";
import api from "../../services/api";
import { CopyToClipboard } from "react-copy-to-clipboard";

import "./styles.css";

export default class Main extends Component {
  constructor() {
    super();
    this.state = {
      tabs: [],
      selectedTab: 0,
      products: [],
      openedItems: [],
      pages: 1,
      page: 1,
      copied: '',
      loading: false,
      requestError: false
    };
  }

  componentDidMount() {
    this.getComponents();
    this.loadProducts();
  }

  setSelectedTab = async (id) => {
    const { selectedTab } = this.state;
    if (selectedTab !== id) {
      await this.setState({ selectedTab: id, products: [], openedItems: [], page: 1 });
      this.loadProducts();
    }
  }

  getComponents = () => {
    const tabs = [
      { id: 0, name: "todos", img: require("../../assets/all.png"), img_sltd: require('../../assets/all_selected.png') },
      { id: 1, name: "cupons", img: require("../../assets/coupon.png"), img_sltd: require('../../assets/coupon_selected.png') },
      { id: 2, name: "promoções", img: require("../../assets/promo.png"), img_sltd: require('../../assets/promo_selected.png') },
      { id: 3, name: "grátis", img: require("../../assets/sample.png"), img_sltd: require('../../assets/sample_selected.png') }
    ];
    this.setState({ tabs });
  };

  loadProducts = async (page = 1) => {
    this.setState({ loading: true });
    const { selectedTab } = this.state;
    await api(page, selectedTab).get().then(
      res => {
        const { data, pages } = res.data;
        let { products } = this.state;
        products = products.concat(data);
        this.setState({ products, pages, page, loading: false, requestError: false });
      },
      err => {
        this.setState({ loading: false, requestError: true });
        console.log(err);
      }
    );
  };

  nextPage = () => {
    const { page, pages } = this.state;
    if (page === pages) return;
    const currentPage = page + 1;
    this.loadProducts(currentPage);
  };

  setOpnStaus = item => {
    const arr = this.state.openedItems;
    if (arr.indexOf(item) > -1) {
      arr.splice(arr.indexOf(item), 1);
      this.setState({ openedItems: arr });
    } else {
      arr.push(item);
      this.setState({ openedItems: arr });
    }
  };

  setCopiedState = (index) => {
    this.setState({ copied: index });
    setTimeout(() => {
      this.setState({ copied: '' });
    }, 300);
  }

  formatProduct = (product) => {
    switch (product.categoria) {
      case '1': //cupom

        //produto com cupom
        if (!product.preco_anterior && product.preco && product.cupom) {
          return <p>com o cupom sai por <strong>R${product.preco}</strong></p>
        }

        //apenas cupom
        else if (!product.preco_anterior && !product.preco && product.cupom) {
          return <p>cupom de <strong>{product.desconto}</strong></p>
        }

        else if (product.preco_anterior && product.cupom) {
          //produto gratuito com cupom
          if (!product.preco) return <p>de R${product.preco_anterior} sai <strong>GRATUITO</strong></p>
          else return <p>de R${product.preco_anterior} por <strong>R${product.preco}</strong></p>
        }

        break;

      case '2': //promoção

        //apenas promoção
        if (product.preco && !product.cupom) {
          return <p>de R${product.preco_anterior} por <strong>R${product.preco}</strong></p>
        }

        //apenas promoção sem preço
        else if (!product.preco) {
          return <p>promoção de <strong>{product.desconto}</strong></p>
        }

        break;

      case '3': //gratuitos
        return <p>aproveite! Está <strong>GRATUITO</strong></p>
      default:
        break;
    }
  }

  render() {
    const {
      tabs,
      selectedTab,
      products,
      loading,
      page,
      pages,
      requestError,
      openedItems
    } = this.state;

    return (
      <div>
        <div className="tabs-container">
          {tabs.map(tab => (
            <div className="tab-item" key={tab.id} onClick={() => this.setSelectedTab(tab.id)}>
              {
                selectedTab !== tab.id ?
                  <div>
                    <div className="item-img" style={{ backgroundImage: "url(" + tab.img + ")" }} />
                    <p>{tab.name}</p>
                  </div>
                  :
                  <div className="selected-container">
                    <div className="item-img" style={{ backgroundImage: "url(" + tab.img_sltd + ")" }} />
                    <p>{tab.name}</p>
                  </div>
              }
            </div>
          ))}
        </div>

        {
          requestError && (
            <div className="request-error">
              <h1>Sem conexão à internet</h1>
              <p>
                Verifique se você está conectado à internet e,
              <br />
                após se conectar, toque no botão a seguir
            </p>
              <button onClick={this.loadProducts}>Recarregar</button>
            </div>
          )
        }
        {
          !requestError && (
            <div className="product-list">
              {products.map((product, index) => (
                <article key={index} className={openedItems.indexOf(index) > -1 ? "opened" : "closed"}>
                  {
                    product.expirado === 1 &&
                    <p className="expired">ops! Acabou ☹</p>
                  }
                  <div className="product-content" onClick={() => this.setOpnStaus(index)} >

                    <div className="product-img" style={{ backgroundImage: "url(" + product.link_imagem + ")" }} />

                    <div className="product-data">
                      <strong>{product.nome}</strong>
                      <p>oferecido por {product.loja}</p>
                      {this.formatProduct(product)}
                    </div>


                    <div className="product-type">
                      <div className="type-icon" style={{
                        backgroundImage: "url(" + tabs[parseInt(product.categoria)].img_sltd + ")"
                      }} />
                      <p>
                        {product.categoria === '1' ? 'cupom' :
                          (product.categoria === '2' ? 'promo' : 'grátis')}
                      </p>
                    </div>


                  </div>


                  {openedItems.indexOf(index) > -1 && (
                    <div className="product-more-info">
                      <div className="desc-container">
                        <p className="product-desc">☛ {product.descricao}</p>
                      </div>

                      {
                        product.categoria === '1' &&
                        <div className="coupon-container">
                          <CopyToClipboard text={product.cupom} onCopy={() => this.setCopiedState(index)}>
                            <span className="coupon">{product.cupom}</span>
                          </CopyToClipboard>
                          <div className="tips">
                            <div className="touch-icon" />
                            <p>Toque no cupom para copiar</p>
                          </div>
                        </div>

                      }
                      {this.state.copied === index ? (
                        <p className="copy-toast">copiado</p>
                      ) : null}
                      {
                        product.categoria === '2' &&
                        <div className="promo-container">
                          <p>ECONOMIA DE <strong>{product.desconto}</strong></p>
                        </div>
                      }

                      {
                        product.categoria === '3' &&
                        <div className="promo-container">
                          <p>+ INFORMAÇÕES E CONDIÇÕES NO SITE</p>
                        </div>
                      }
                      <a href={product.link} className="btn-url">acessar</a>

                    </div>
                  )}
                </article>
              ))}
              <div className="navigation">
                {loading && <div className="loading-image" />}
                {!loading && page !== pages && (
                  <button onClick={this.nextPage}>Carregar mais promoções</button>
                )}
                {
                  !loading && page === pages && (
                    <p>Não há mais promoções</p>
                  )}
              </div>
            </div>
          )
        }
      </div >
    );
  }
}
